$bgColor: #202020;

.clients {
    background-color: $bgColor;
    padding: 55px 0 80px;
}

.clients__wrap {
    padding: 55px 0 5px;
}

.clients__title {
    font-size: 30px;
    font-family: "Raleway";
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.1;
    text-align: center;
    margin: 25px 0;

    &::before {
        background-color: $text;
        content: '';
        display: block;
        width: 70px;
        height: 2px;   
        margin: 0 auto 25px;  
    }
  }

.clients__text {
    font-size: 14px;
    color: $text;
    line-height: 1.429;
    text-align: center;
    max-width: 555px;
    margin: 25px auto;
}
  
.clients__carousel {
    margin: 50px 0;
    padding: 0 110px;
    box-sizing: border-box;
}

@media(max-width: 980px) {
    .clients__carousel {
        padding: 0 50px;
    }
}
@media(max-width: 480px) {
    .clients__wrap {
        padding: 30px 0 10px;
    }
}


  