$circleBg: rgb(54, 54, 54);
$itemBg: #636363;
$colorTitle: #636363;
$colorText: #555555;

.advantages__title {
    @include visually-hidden;
}

.advantages__list {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, auto));
    grid-column-gap: 30px;
}

.advantages__item {
    margin: 80px 0;

    &:hover {
       * {
           transition-duration: 500ms;
       } 
        .advantages__img {
            border-color: $second;
            border-width: 2px;
            height: 102px;
        } 
        .advantages__svg {
            fill: $second;
            transition-duration: 90ms;
        }

        .advantages__subtitle {
            color: $second;
        }
        .advantages__text {
            color: $white;
        }
    }
}

.advantages__img {
    border-style: solid;
    border-width: 1px;
    border-color: $circleBg;
    border-radius: 50%;
    width: 104px;
    height: 104px;
} 

.advantages__svg {
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 40px;
    fill: $itemBg;
    max-width: 160px;

}

.advantages__subtitle {
    font-size: 18px;
    font-family: "Raleway";
    color: $colorTitle;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1.111;
    margin: 20px 0;
}

.advantages__text {
    font-size: 14px;
    font-family: "Roboto";
    color: $colorText;
    line-height: 1.429;
    margin: 20px 0;

    &::after {
        background-color: $second;
        width: 70px;
        height: 2px;
        content: '';
        display: block;
        margin-top: 25px;
    }
}


@media (max-width: 960px) and (min-width: 660px) {
    .advantages__list {
        grid-template-columns: repeat(2, auto);
        grid-column-gap: 30px;
    }
    .advantages__item {
        margin: 30px 0;
    }    
    
}

@media (max-width: 480px) {
    .advantages__item {
        margin: 30px 0;
        text-align: center;
    }
    .advantages__img {
        margin: 0 auto;
    }
    
}
  

  
  
  