.contacts {
    background-color: $fruit;
}

.contacts__wrap {
    padding: 55px 0 60px;
    
} 

.contacts__title {
    font-size: 30px;
    font-family: "Raleway";
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.1;
    text-align: center;
    margin: 25px 0 40px;

    &::before {
        content: '';
        background-color: $white;
        display: block;
        margin: 0 auto 25px;
        width: 70px;
        height: 2px;
    }
  }

.contacts__list {
    margin: 0 10px ;
    list-style: none;
    padding: 0;
    display: flex;
}

.contacts__form {
    flex-basis: 535px;
    margin: 10px 65px 10px 0;
}

.contacts__map {
    margin: 10px 0;
}
.contacts__map img {
    flex-grow: 1;
    max-width: 570px;
    height: 415px;
}
  
@media (max-width: 980px) {
    .contacts__list {
        flex-direction: column;
    }

    .contacts__form {
        flex-basis: auto;
        margin-right: 0;
        margin-bottom: 35px;
    }

    .contacts__map img {
        max-width: 100%;
        height: auto;
    }
}

@media (max-width: 480px) {
    .contacts__wrap {
      padding: 30px 0;
    }
    .contacts__map img {
        width: 100%;
        height: 500px;
        object-fit: cover;
    }
}