// banner
$color-text: #555;

.banner__wrap {
    background: url('../img/watch/mainclock.png') left top no-repeat;
    background-size: contain;
}

#banner__watch {
    margin: 0 ;
    margin-left: 20px;
    padding: 0;
    display: none;
    width: 80%;
    max-width: 420px;
}
.banner__wrap {
    padding: 255px 0 250px 0;
}

.banner__info {
    max-width: 580px;
    margin-left: auto;
}

.banner__info  a{
    appearance: none;
}

.banner__title {
    font-size: 80px;
    font-family: "Raleway";
    color: $white;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1;
    margin: 0;
}

.banner__little {
    font-size: 43px;
    display: block;
}

.banner__red {
    color: $second;
}

.banner__text {
    font-size: 18px;
    font-family: "Raleway";
    color: rgb(85, 85, 85);
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1.333;   
    margin: 15px 0;
}

.banner__button {
    @include button() {
        display: inline-block;
        margin: 20px 25px 20px 0;
    };
}
.banner__button--last {
    margin-right: 0;
    border-color: $white;
    background-color: transparent;
}

@media (max-width: 1062px) {
    .banner__text {
        text-align: center;
    }
}

@media (max-width: 980px) {
    .banner__wrap {
        background: none;
    }
    .banner__wrap {
        padding-top: 70px;
        padding-bottom: 50px;

    }
    .banner__info {
        max-width: 580px;
        margin: auto;
        
    }
    #banner__watch {
        display: block;
        margin: 0 auto;
        padding-right: 25px;
    }
    .banner__text {
        text-align: left;
    }
}
@media (max-width: 768px) {
    .banner__title {
        font-size: 50px;
    }
    .banner__little {
        font-size: 25px;

    }
    .banner__text {
        font-size: 16px;
    }
}
@media (max-width: 480px) {
    .banner__info {
        margin-left: 10px;
    }
    
    .banner__title {
        font-size: 40px;
    }
    .banner__little {
        font-size: 20px;

    }
    .banner__text {
        font-size: 14px;
    }
    .banner__button {
        margin: 10px 15px 10px 0;
    }
    .banner__button--last {
        margin-right: 0;
    }
}  
  