$desTextColor: #959595;

.blog__subtitle {
    @include visually-hidden();
}

.blog__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(5, minmax(130px, auto));
    list-style: none;
    padding: 115px 0 150px;
}

.blog__element--three {
    grid-column: span 3;
}
.blog__element--four {
    grid-column: span 4;
}

.blog__element--five {
    grid-column: span 5;
}

.blog__element--eight {
    grid-column: span 8;
}

.blog__element {
position: relative;
    &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 50%;
        background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.6) 50%, rgba(0,0,0,1) 100%);
        z-index: 1;
    }

    &:nth-child(1) {
        background: url('../img/blog1.jpg') center no-repeat;
        background-size: cover;
        grid-row: 1 / 4;

        .blog__title {
            font-size: 28px;
        }
    }

    &:nth-child(2) {
        grid-row: 1 / 3;

        .blog__title {
            font-size: 28px;
            position: relative;

            margin: 20px 0;

            &::before {
                content: '';
                background-color: $text;
                position: absolute;
                left: 0;
                top: -30px;
                width: 70px;
                height: 2px;
              }
        }
        &::before {
            display: none;
        }
    }

    &:nth-child(3) {
        background: url('../img/blog3.jpg') center no-repeat;
        background-size: cover;
        grid-row: 4 / 6;
    }

    &:nth-child(4) {
        background: url('../img/blog4.jpg') center no-repeat;
        background-size: cover;
        grid-row: 4 / 6;
    }
    &:nth-child(5) {
        background: url('../img/box.jpg') center no-repeat;
        background-size: cover;
        grid-row: 3 / 6;
    }
    &:hover {
        * {
            transition-duration: 500ms;
        }
        .blog__info {
            border-color: $second;

        }
        .blog__link {
            background-color: $second;
        }
    }
}

.blog__title{
    font-size: 24px;
    font-family: "Raleway";
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    margin: 20px 0;
    text-decoration: none;
  }
  

  .blog__text {
    font-size: 14px;
    color: $text;
    line-height: 1.429;
  }

  .blog__description {
    font-size: 12px;
    color: $desTextColor;
    text-transform: uppercase;
    line-height: 1.167;
   
    &::after {
        background-color: $second;
        width: 70px;
        height: 2px;
        content: '';
        margin-top: 25px;
        display: block;
      }

    span {
        color: $white;
    }
  }
  
  .blog__link {
    background-color: $black;
    width: 41px;
    height: 41px;
    display: block;
    position: relative;
    margin: 15px 0 20px;
    
    &:focus {
        background-color: $second;
    }

    & svg {
    fill: $white;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 21px;
    height: 17px;
    transform: translate(-50%, -50%);   
    }
  }
  
  .blog__info {
      padding: 10px 25px;
      position: relative;
      z-index: 2;
      height: 100%;
      box-sizing: border-box;
      border: 5px solid transparent;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
  }
  

  @media(max-width: 980px) {
      .blog__element {
        &:nth-child(1), &:nth-child(2) {
            .blog__title {
                font-size: 20px;
            }
        } 
        .blog__title {
            font-size: 20px;
        }
    }
  }
  @media(max-width: 768px) {
    .blog__list {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
        padding: 115px 0 150px;
    }
    .blog__element--three, .blog__element--four, .blog__element--five, .blog__element--eight {
        grid-column: span 1;
        grid-row: span 1 !important;
    }
}

@media(max-width: 768px) {
    .blog__list {
        grid-template-columns: minmax(290px, auto);
        padding: 50px 0 55px;
    }
    .blog__first{
        grid-row: 1 / 2 !important;
    }
}