$inputBg: #202020;

.form {
    label {
        @include visually-hidden;
    }
}

.form__input {
    input, textarea {
        border-radius: 10px;
        background-color: $inputBg;
        height: 60px;
        width: 100%;
        display: block;
        border: none;
        box-shadow: none;
        resize: none;
        box-sizing: border-box;

        font-size: 20px;
        font-family: "Raleway";
        color: $white;
        line-height: 1.4;
        padding: 16px 10px 16px 30px;
        margin-bottom: 20px;

        &::placeholder {
            color: $white;
            opacity: 1;    
            appearance: none;
        }
      }
      
}

textarea {
    min-height: 175px;
}

.form__button{
    border-radius: 10px;
    background-color: $second;
    width: 100%;
    padding: 16px;
    appearance: none;
    border: none;
    box-shadow: none;
    cursor: pointer;

    font-size: 20px;
    font-family: "Raleway";
    color: $white;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1.4;
    text-align: center;
    transition-duration: 500ms;

    &:hover {
        background-color: darken($second, 15%);
    }
  }
  