

.resposive__wrap {
    padding: 100px 0;
    background: url('../img/man.png') left bottom no-repeat;
    background-size: contain;
}

.resposive__info {
    max-width: 360px;
    margin-left: auto;
}

.resposive__title {
    font-size: 30px;
    font-family: "Raleway";
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.1;
    margin: 25px 0;
    position: relative;
    
    &::before {
        background-color: $text;
        position: absolute;
        width: 70px;
        height: 2px;
        left: 0;
        top: -25px;
        content: '';
    }
  }

.resposive__text {
    font-size: 14px;
    color: $text;
    line-height: 1.429;
    margin: 20px 0;
}

.resposive__list {
    margin: 20px 0;
}

.responsive__button {
    @include button($fruit) {
        margin-top: 50px;
    };
}

@media (max-width: 768px) {

    .resposive__wrap {
        background-image: none;
    }

    .resposive__info {
        margin-left: 10px;
        max-width: 100%;
    }
}

@media (max-width: 480px) {
    .resposive__wrap {
    padding: 50px 0;
    }
}


  
  