    //menu

.menu {
    display: flex;
    list-style: none;
    flex-wrap: wrap;

}

.menu__element {
    margin-right: 35px;
}
.menu__element:last-child {
    margin-right: 5px;
}

.menu__link {
    font-size: 12px;
    font-family: "Raleway";
    color: $text;
    text-transform: uppercase;
    line-height: 1.667;
    display: inline-block;
    text-decoration: none;
    transition-duration: 500ms;
    
    &::before {
        content: '';
        width: 8px;
        height: 2px;
        background-color: $second;
        display: block;
        margin-bottom: 2px;
        opacity: 0;
        transition-duration: 500ms;
    }
    
    &:hover {
        color: $second;


        &::before {
            opacity: 1;
        }
    }
  }


@media (max-width: 768px) {
    .menu__element {
        margin-right: 20px;
    }
    .menu__element:last-child {
        margin-right: 3px;
    }
}