$bg: #202020;
$descriptionColor: #959595;

.works {
    background-color: $bg;
}

.works__title-hide {
    @include visually-hidden;
}

.works__list {
    padding: 80px 0 160px 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(12, minmax(60px, auto));
    grid-auto-rows: minmax(270px, auto);
}

.works__element--three {
    grid-column: span 3;
}

.works__element--four {
    grid-column: span 4;
}
.works__element--five {
    grid-column: span 5;
}
.works__element--six {
    grid-column: span 6;
}



.works__title {
    font-size: 30px;
    font-family: "Raleway";
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.1;
    position: relative;
    margin: 25px 0;
    margin-left: 5px;

    &::before {
        background-color: $text;
        position: absolute;
        width: 70px;
        height: 2px;
        left: 0;
        top: -25px;
        content: ''; 
    }
  }

  .works__text {
    font-size: 14px;
    color: $text;
    line-height: 1.429;
    margin: 20px 6px;
  }

  .works__element {

    position: relative;

    &:first-child {
        max-width: 270px;

        &::before {
            display: none;
        }
    }

    &::before {
        background-color: rgba(17, 17, 17, 0.85);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        opacity: 0;
        content: '';
        transition-property: opacity;
        transition-duration: 500ms;
    }

    &:hover {
        &::before {
            z-index: 1;
            opacity: 1;
        }
        .works__info {
            border-color: $second;
            z-index: 2;
            opacity: 1;
        }
    }

    &:nth-child(2) {
        background: url('../img/watch/1.jpg') center no-repeat;
        background-size: cover;
    }
    &:nth-child(3) {
        background: url('../img/watch/2.jpg') center no-repeat;
        background-size: cover;
    }
    &:nth-child(4) {
        background: url('../img/watch/3.jpg') center no-repeat;
        background-size: cover;
    }
    &:nth-child(5) {
        background: url('../img/watch/4.jpg') center no-repeat;
        background-size: cover;
    }
    &:nth-child(6) {
        background: url('../img/watch/5.jpg') center no-repeat;
        background-size: cover;
    }


  }
  
.works__info {
    width: 100%;
    height: 100%;
    position: relative;
    border: 5px solid transparent;
    box-sizing: border-box;
    padding: 10px 25px 80px;
    z-index: -1;
    opacity: 0;
    transition-duration: 500ms;
}

.works__name {
    font-size: 24px;
    font-family: "Raleway";
    color: $white;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.1;
    margin: 20px 0;
  }
  
.works__description {
        font-size: 12px;
        color: $descriptionColor;
        text-transform: uppercase;
        line-height: 1.667;

        &::after { 
                background-color: $second;
                width: 70px;
                height: 2px;
                display: block;
                z-index: 1;
                opacity: 1;
                margin-top: 20px;
                content: '';
              }
              
}

.works__zoom, .works__link {
        border-radius: 50%;
        background-color: $primary;
        position: relative;
        cursor: pointer;
        display: inline-block;
        width: 42px;
        height: 42px;
        margin: 0 2px;
        appearance: none;
        padding: 0;
        border: none;   
        transition-duration: 500ms;;

        svg {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            fill: $white;
            width: 15px;
            height: 15px;
        }

        &:hover, &:active {
            background-color: $second;
        }
}

.works__btns {
    position: absolute;
    right: 30px;
    bottom: 30px;
}

.works__element--active {
    &::before {
        z-index: 1;
        opacity: 1;
    }
    .works__info {
        border-color: $second;   
        opacity: 1;
        z-index: 2;
    }
}

@media(max-width: 980px) {
    .works__title {
        font-size: 24px;
    }
    .works__name {
        font-size: 18px;
    }
}

@media(max-width: 780px) {
    .works__list {
        grid-template-columns: repeat(2, minmax(145px, auto));
    }
    .works__element {
        grid-column: span 1;
        &:first-child {
        grid-column: 1 / -1;
        max-width: 100%;
        }
    }

}

@media(max-width: 490px) {
    .works__list {
        grid-template-columns: minmax(280px, auto);
        padding: 40px 0 80px 0;
    }
    .works__element {
        grid-column: span 1;
        &:first-child {
        grid-column: 1 / -1;
        max-width: 100%;
        }
    }

}

