    // Header
.header {
    border-bottom: 4px solid $second;
}

.header__wrap  {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 55px 0;
}

.header__logo h1 {
    margin: 0;


}
.header__logo  {
    margin: 0;
    margin-left: 20px;
    margin-right: 20px;
    text-decoration: none;
    color: $white;
}

.header__menu {
    margin: 0;
    padding: 0;
}

@media (max-width: 768px) {
    .header__logo {
        margin-right: 0;
        margin-left: 0;
        margin-bottom: 20px;
    }
    .header__wrap {
        flex-direction: column;
        justify-content: flex-start;
        padding: 20px 0 20px 0;
    }
}