.skills {
    background-color: $second;
}
.skills__wrap {
    padding: 80px 0;
    display: flex;
}

.skills__left {
    flex-basis: 280px;
    margin-right: 115px;
    margin-left: 10px;
}

.skills__right {
    flex-grow: 1;
}


.skills__title {
    font-size: 28px;
    font-family: "Raleway";
    color: $white;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1.1;
    position: relative;
    margin: 25px 0;


    &::before {
        background-color: $white;
        position: absolute;
        left: 0;
        top: -25px;
        width: 70px;
        height: 2px;  
        content: ' ';
    }
}

.skills__text {
    font-size: 14px;
    color: $white;
    line-height: 1.429;
    margin: 20px 0;
}

.skills__button {
    @include button($primary) {
        margin-top: 50px;
    };
}

.skills__progress {
    margin: 20px 0;
}

@media (max-width: 980px) {
    .skills__left {
        margin-right: 30px;
    }
}

@media (max-width: 768px) {
    .skills__wrap {
        flex-direction: column;
    }

    .skills__left, .skills__right {
        margin: 0 10px;
        flex-basis: auto;
    }
}

@media (max-width: 480px) {
    .skills__wrap {
        padding: 40px 0;
    }
}

  